.quit {
    width: calc(100% - 22px);
    position: absolute;
    text-align: center;
    height: calc(50% - 135px);
    border:11px double aqua;
    background: #000574;
    z-index: 10;
    top: 50%;
    transform: translate(0, calc(-50% + 5px));
}

.quit-button {
    position: absolute;
    top: 65%;
    padding: 10px;
}
.quit-yes {
    left: 40%;
}

.quit-no {
    left: 60%;
}

.quit-selected {
    background: white;
    color: black;
}